import { Injectable } from '@angular/core';
import { RequestOptions, Response, Headers, Http } from '@angular/http'
import { map, catchError } from '../../../../../node_modules/rxjs/operators';
import { Observable } from '../../../../../node_modules/rxjs';

@Injectable({
  providedIn: 'root'
})
export class KitchenDashboardService {
  private baseUrl: string = 'http://localhost:8080/api';

  private headers = new Headers({ 'Content-Type': 'application/json' });

  private options = new RequestOptions({ headers: this.headers });

  constructor(private http: Http) { }

  closeKitchen(date:Date) :Observable<any>{
    return this.http.get(this.baseUrl + '/closeKitchen/'+date, this.options)
      .pipe(map((response: Response) => response.json()),
        catchError((error: any) => {
          return Observable.throw(error)
        }));

  }
}
