import { Component, OnInit } from '@angular/core';
import { MenuMaster } from '../../../admin/KOT/menu-master/menu-master'
import { MenuMasterService } from '../../../admin/KOT/menu-master/menu-master.service';
import { DashboardService } from '../../../admin/dashboard/dashboard.service';
import { TempRecipes } from '../../../admin/dashboard/temp-recipes';
import { DatePipe } from '@angular/common';
@Component({
  selector: 'app-available-menu',
  templateUrl: './available-menu.component.html',
  styleUrls: ['./available-menu.component.css'],
  providers: [DatePipe]
})
export class AvailableMenuComponent implements OnInit {

  private menuMaster: MenuMaster[];
  private menuMasters: MenuMaster[];
  private tempRecipes: TempRecipes[];
  private quantity = [];
  private date;
  private dispaly;
  Option = [{ menuId: '', menuName: '', quantity: '', availableMenuQuantity: '', precentquantity: '' }]
  constructor(private datePipe: DatePipe,
    private menuMasterService: MenuMasterService,
    private dashboardService: DashboardService) { }

  ngOnInit() {
    this.date = this.datePipe.transform(new Date(), 'yyyy-MM-dd');
    this.getAllTempRecipes();
    this.getAllMenuMaster();
  }

  getAllTempRecipes() {
    this.dashboardService.getAllTempRecipes().subscribe((data) => {
      this.tempRecipes = data;
      var i = 0;
      var quantity;
      if (this.tempRecipes.length == 0) {
        this.dispaly = "Today Menu don't Available"
      }
      this.tempRecipes.forEach(res => {
        if (res.unit == 'KiloGram') {
          quantity = res.quantity;
          quantity = quantity / 1000;
        } else {
          quantity = res.quantity;
        }
        this.Option[i].quantity = quantity.toString();
        this.quantity[i] = quantity;
        this.Option.push({ menuId: '', menuName: '', quantity: '', availableMenuQuantity: '', precentquantity: '' });
        i++;
      });
      this.getAllMenuMaster();
    }, (error) => {
      console.log(error);
    });
  }
  getAllMenuMaster() {
    this.menuMasterService.getMenuMaster().subscribe((data) => {
      this.menuMaster = data;
      let i = 0;
      let qty = 0;
      this.menuMaster.forEach(res => {
        if (res.date == this.date) {
          this.Option[i].menuId = res.menuId.toString();
          this.Option[i].menuName = res.menuName;
          this.Option[i].availableMenuQuantity = res.availableMenuQuantity.toString();
          this.Option.push({ menuId: '', menuName: '', quantity: '', availableMenuQuantity: '', precentquantity: '' });
          qty = Number(this.Option[i].availableMenuQuantity) / Number(this.Option[i].quantity) * 100;
          this.Option[i].precentquantity = qty.toString();
          i++;
        }
      });
    }, (error) => {
      console.log(error);
    });
  }

}
