import { Component, OnInit } from '@angular/core';
import { DatePipe, Time } from '@angular/common';
import {KitchenDashboardService} from './kitchen-dashboard.service';
@Component({
  selector: 'app-kitchen-dashboard',
  templateUrl: './kitchen-dashboard.component.html',
  styleUrls: ['./kitchen-dashboard.component.css'],
  providers: [DatePipe]
})
export class KitchenDashboardComponent implements OnInit {

  private date;
  private time;
  constructor(private datePipe: DatePipe,
    private kitchenDashboardService:KitchenDashboardService) { }

  ngOnInit() {
    
  }

  closeKitchen(){
    this.time = this.datePipe.transform(new Date(), 'hh:mm:ss');
    this.date = this.datePipe.transform(new Date(), "yyyy-MM-dd");
    this.kitchenDashboardService.closeKitchen(this.date).subscribe((data)=>{
      console.log(data);
    },(error)=>{
      console.log(error);
    })
  }
  open_in_new
}
